.userMain {
  width: 79%;
  position: absolute;
  left: 20%;
  .drop {
    position: relative;
    float: right;
    width: 225px;
    background: transparent;
    border-radius: 3px;
    margin-top: 15px;
    .d {
      position: absolute;
      top: 0;
      width: 100%;
      height: 44px;
      background-color: transparent;
      .icon {
        display: none;
      }
      .divider {
        width: fit-content;
        border: none;
      }
      .menu {
        top: 112%;
        width: 100%;
        @media (max-width: 480px) {
          right: -5%;
          top: 115%;
        }
      }
      .item {
        padding: 0 16px;
        a {
          font-family: "Metropolis";
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 19px;
          color: #667c8a;
        }
        button {
          border: none;
          outline: none;
          font-family: "Metropolis";
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 19px;
          color: #667c8a;
          background-color: transparent;
        }
      }
    }
    .down {
      display: none;
      text-align: center;
      &.active {
        display: block;
        padding-bottom: 10px;
      }
      p {
        width: 90%;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        border-radius: 3px;
        color: #637381;
        margin: 0 auto;
        padding: 10px 0;
        // &:nth-child(2) {
        // }
        &:hover {
          background-color: white;
        }
      }
    }
    .shown {
      width: 100%;
      display: flex;
      align-items: center;
      height: 50px;
      justify-content: center;
      cursor: pointer;
      h1 {
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #637381;
        margin: 0 10px;
      }
      i.icon {
        margin-top: -4px;
        margin-right: 8px;
        color: #637381;
      }
      i.icon.user.circle:before {
        font-size: 24px;
      }
      i.icon.angle.down {
        display: block;
        font-size: 16px;
        margin-top: -2.4px;
        margin-right: 0;
      }
    }
  }
  .contain {
    width: 98%;
    margin: auto;
    margin-right: 0;
    h1 {
      padding-top: 75px;
      margin: 0;
      font-size: 40px;
      line-height: 47px;
      padding-bottom: 30px;
      color: #e40055;
    }
    .Navigation {
      display: flex;
      align-items: center;
      margin-bottom: 15px;
      .links {
        background-color: transparent;
        border-radius: 3px;
        padding: 13px 0;
        width: fit-content;
        .searchBox {
          width: 450px;
          display: flex;
          // height: 45px;
          position: relative;
          // align-items: center;
          button {
            border: 1px solid #e40051;
            outline: none;
            background-color: #e40051;
            position: absolute;
            height: 39.5px;
            right: 0px;
            width: 44px;
            border-radius: 3px;
            top: 8px;
            cursor: pointer;
            i.icon {
              color: #fff;
              margin-right: 0;
            }
            &:disabled {
              cursor: not-allowed;
              background-color: #919eab;
              border-color: #919eab;
            }
          }
          .text-input {
            width: 100%;
          }
          input {
            border: 1px solid #919eab;
            box-sizing: border-box;
            border-radius: 3px;
            &:focus,
            &:valid ~ label,
            &:not([value=""]) ~ label {
              outline: none;
              border-color: #e40055;
              box-shadow: none;
            }
            &:focus ~ label {
              top: 0px;
              left: 8px;
              font-size: 12px;
              background-color: white;
              padding-left: 5px;
              padding-right: 5px;
              height: none;
              color: #e40055;
            }
            &:not([value=""]) ~ label {
              font-size: 12px;
            }
          }
          label {
            font-weight: normal;
            font-size: 14px;
            line-height: 16px;
            color: #919eab;
            top: 20px;
          }
        }
      }
      .redBtn {
        margin-left: 16px;
        border-radius: 3px;
        font-size: 14px;
        font-weight: bold;
        line-height: 16px;
        padding: 11px 42px;
        background-color: transparent;
        border: 1px solid #e40051;
        color: #e40055;
        i.icon {
          font-size: 16px;
        }
      }
    }
    table.mainData {
      border: 1px solid #dadada;
      box-sizing: border-box;
      box-shadow: 5px 9px 50px rgba(130, 130, 130, 0.15);
      border-radius: 3px;
      width: 75%;
      padding: 18px 0;
      padding-bottom: 10px;
      border-collapse: inherit;
      border-spacing: 0;
      padding-bottom: 0;
      //   text-indent: ;
      .border {
        width: 100%;
        border: 1px solid #dadada;
      }
      tr {
        width: 100%;
        // height: 30px;
        // display: flex;
        // align-items: center;
        // padding: 23px 36px;
        // border-bottom-color: red !important;
        &.empty {
          // word-break: break-all;
          height: 60px;
          border-bottom: 1px solid #dadada;
          text-align: center;
          position: relative;
          td {
            font-weight: normal;
            font-family: "Metropolis";
            font-size: 16px;
            line-height: 19px;
            color: #667c8a;
            position: absolute;
            left: 45%;
            top: 60%;
          }
        }
        th {
          width: 12%;
          font-weight: normal;
          font-size: 14px;
          line-height: 16px;
          color: #919eab;
          padding-bottom: 5px;
          text-align: start;
          font-family: "Metropolis";
          &:nth-child(1) {
            padding-left: 25px;
            width: 25%;
          }
          &:nth-child(2) {
            width: 38%;
          }
          &:nth-child(3) {
            width: 20%;
          }
        }
        td {
          font-family: "Metropolis";
          font-weight: normal;
          font-size: 16px;
          line-height: 19px;
          color: #667c8a;
          // word-break: break-all;
          word-break: break-word;
          height: 60px;
          border-bottom: 1px solid #dadada;
          padding-left: 5px;
          &:nth-child(1) {
            padding-left: 25px;
          }
          &:nth-child(4) {
            font-size: 14px;
            line-height: 16px;
            display: flex;
            align-items: center;
            color: #d83621;
            cursor: pointer;
            img {
              margin-right: 10px;
              margin-bottom: 4px;
            }
          }
          //   &:nth-child(7) {
          //     width: 6%;
          //   }
          &:nth-child(3) {
            text-transform: capitalize;
          }
          //   &:nth-child(6) {
          //     width: 8%;
          //   }
          //   &:nth-child(2) {
          //     width: 8%;
          //   }
          //   &:nth-child(5),
          //   &:nth-child(4) {
          //     width: 8%;
          //   }
          .select {
            width: 90%;
            position: relative;
            .dots {
              position: absolute;
              top: 10px;
              right: -11px;
              cursor: pointer;
              padding: 0;
              margin: 0;
              width: 0;
              border: none;
              .visible {
                &.menu {
                  width: 160px;
                  left: -140px;
                  .text {
                    padding-bottom: 0;
                  }
                  i.icon,
                  i.icons {
                    font-size: 13px;
                    width: 18px;
                    margin-right: 3px;
                  }
                }
              }
              i.icon,
              i.icons {
                font-size: 20px;
                color: #637381;
                margin: 0;
                width: 0;
                &.ellipsis.vertical:before {
                  color: rgba(0, 0, 0, 0.54);
                  // background-color: rgba(0, 0, 0, 0.54) !important;
                  background-color: transparent !important;
                }
                &:before {
                  background: transparent !important;
                }
              }
              .ui.button > .icon:not(.button) {
                margin: 0;
                width: 0;
              }
            }
            .set {
              position: absolute;
              width: 130%;
              color: #667c8a;
              padding: 10px 5px;
              margin-top: 2px;
              border: 0.5px solid #637381;
              box-sizing: border-box;
              border-radius: 3px;
              display: flex;
              flex-flow: column;
              align-items: center;
              background-color: white;
              z-index: 80;
              p {
                border: none;
                display: flex;
                align-items: center;
                cursor: pointer;
                span {
                  margin-right: 8px;
                }
              }
            }
            .opt {
              position: absolute;
              top: 0;
              width: 100%;
              color: #667c8a;
              padding: 10px;
              border: 0.5px solid #637381;
              box-sizing: border-box;
              border-radius: 3px;
              display: flex;
              flex-flow: column;
              align-items: center;
              background-color: white;
              z-index: 80;
              // justify-content: center;
              input {
                font-family: "Railway", sans-serif;
                font-style: normal;
                font-weight: normal;
                font-size: 15px;
                line-height: 17px;
                width: 100%;
                padding: 10px;
                color: #667c8a;
                border: none;
                background-color: transparent;
                margin-bottom: 10px;
                box-sizing: border-box;
                border-radius: 3px;
                outline: none;
                cursor: pointer;
                &:hover {
                  background-color: #667c8a;
                  color: white;
                }
                &:nth-child(4) {
                  margin-bottom: 0;
                }
              }
            }
            h1 {
              width: 100%;
              font-weight: normal;
              font-size: 15px;
              line-height: 17px;
              margin: 0;
              color: #667c8a;
              cursor: pointer;
              padding: 10px;
              border: 1px solid transparent;
              box-sizing: border-box;
              border-radius: 3px;
              display: flex;
              align-items: center;
            }
            .p {
              width: 100%;
              font-weight: normal;
              font-size: 15px;
              line-height: 17px;
              margin: 0;
              color: #667c8a;
              cursor: pointer;
              padding: 10px;
              border: 0.5px solid #637381;
              box-sizing: border-box;
              border-radius: 3px;
              display: flex;
              align-items: center;
              .divider {
                width: fit-content;
                border: none;
              }
              .dropdown.icon {
                top: 0.9em;
              }
              .text {
                display: flex;
                align-items: center;
                padding-bottom: 0;
                img {
                  width: 15px;
                  height: 15px;
                }
              }
              .menu > .item {
                font-weight: normal;
                font-size: 15px;
                line-height: 17px;
                margin: 0;
                color: #667c8a;
                font-family: "Railway", sans-serif;
                font-style: normal;
                display: flex;
                align-items: center;
                img {
                  width: 15px;
                  height: 15px;
                }
              }
            }
            .circle {
              width: 15px;
              height: 15px;
              margin-right: 5px;
              margin-bottom: 1.5px;
            }
            .spanRight {
              position: absolute;
              right: 7%;
            }
          }
          .circle {
            width: 15px;
            height: 13.5px;
            border-radius: 50px;
            margin-right: 5px;
            &.a {
              background-color: #3cbb00;
            }
            &.b {
              background-color: #f57a00;
            }
            &.c {
              background-color: #667c8a;
            }
            &.d {
              background-color: #282c36;
            }
          }
          .booked {
            font-weight: normal;
            font-size: 15px;
            line-height: 17px;
            // margin-left: 10px;
            color: #667c8a;
            display: flex;
            align-items: center;
            padding: 0 10px;
            span {
              width: 15px;
              height: 13.5px;
              margin-right: 5px;
              .circle {
                width: 15px;
                height: 15px;
                border-radius: 50px;
                &.b {
                  background-color: #f57a00;
                }
              }
            }
          }
          .selBox {
            border: 0.5px solid #637381;
            box-sizing: border-box;
            border-radius: 3px;
            background-color: transparent;
            padding: 10px;
            width: 85%;
            display: flex;
            align-items: center;
            select {
              width: 100%;
              border: none;
              background-color: transparent;
              outline: none;
              font-weight: normal;
              font-size: 15px;
              line-height: 17px;
              // margin-left: 10px;
              text-align: center;
              color: #667c8a;
              // right: 5px;
              // -webkit-appearance: none;
              option {
                border: 0.5px solid #637381;
                box-sizing: border-box;
                border-radius: 3px;
                background-color: transparent;
                padding: 10px;
                // &[value="Available"] {
                //   // color: yellow;
                //   background-image: url(../image/down.png);
                // }
              }
            }
          }
          .MuiFormControl-root,
          .MuiInput-root {
            width: 95%;
          }
          button {
            padding: 10px 23px;
            outline: none;
            border: none;
            background-color: transparent;
            font-family: "Metropolis";
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 16px;
            cursor: pointer;
            color: #667c8a;
            display: flex;
            align-items: center;
            // justify-content: center;
            span {
              margin-right: 10px;
              width: 18px;
              height: 18px;
              display: flex;
              align-items: center;
              img {
                width: 18px;
                height: 18px;
              }
            }
          }
        }
        &:last-child {
          td {
            border-bottom-color: transparent;
          }
        }
      }
    }
  }
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input:-webkit-autofill {
  box-shadow: 0 0 0px 1000px #fff inset;
  -webkit-text-fill-color: #919eab;
  transition: background-color 5000s ease-in-out 0s;
}
input:-webkit-autofill:focus {
  box-shadow: 0 0 0px 1000px #fff inset;
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: #919eab;
}

.text-input {
  position: relative;
  padding: 0;
  display: block;
  .error-text {
    text-align: start;
    color: red;
    font-size: 12px;
    line-height: 14px;
    margin: 0;
    padding-bottom: 5px;
    padding-left: 5px;
  }
  label {
    position: absolute;
    top: 22px;
    left: 15px;
    padding: 0;
    color: #919eab;
    pointer-events: none;
    transition: 0.5s;
    z-index: 10;
    margin-bottom: 0;
    font-family: "Metropolis";
    font-style: normal;
    font-weight: 200;
    font-size: 16px;
    line-height: 18px;
    span {
      color: #e40055;
    }
  }
  .input {
    width: 100%;
    height: 40px;
    border: 1px solid #919eab;
    outline: none;
    box-sizing: border-box;
    border-radius: 0;
    margin: 8px 0;
    padding: 0 15px;
    font-size: 14px;
    line-height: 16px;
    font-family: "Metropolis";
    font-style: normal;
    font-weight: 200;
    color: #919eab;

    &::-webkit-input-placeholder,
    &:-moz-placeholder,
    &:-ms-input-placeholder {
      color: #919eab;
      font-size: 14px;
    }

    &:focus ~ label,
    &:not([value=""]) ~ label {
      top: 0px !important;
      left: 8px;
      font-size: 12px;
      background-color: white;
      padding-left: 5px;
      padding-right: 5px;
      height: none;
      color: #e40051;
    }

    &:focus ~ label {
      color: #e40055;
    }

    &:focus,
    &:valid ~ label,
    &:not([value=""]) ~ label {
      outline: none;
      border-color: #e40055;
      box-shadow: none;
    }
  }

  textarea.textfield {
    height: 100px;
    width: 100%;
    outline: none;
    border: 1px solid #919eab;
    box-sizing: border-box;
    border-radius: 0;
    margin: 8px 0;
    resize: none;
    padding: 10px 15px;
    font-size: 16px;
    line-height: 18px;
    font-family: "Metropolis";
    font-style: normal;
    font-weight: 200;
    color: #919eab;

    &:focus ~ label {
      color: #e40055 !important;
    }

    &:focus ~ label {
      color: #e40055;
      top: 0px;
      left: 8px;
      font-size: 12px;
      background-color: white;
      padding-left: 5px;
      padding-right: 5px;
      height: none;
      color: #919eab;
    }

    &:focus,
    &:valid ~ label,
    &:not([value=""]) ~ label {
      outline: none;
      border-color: #e40055;
      box-shadow: none;
    }

    &.has-value ~ label {
      top: 0px;
      left: 8px;
      font-size: 12px;
      background-color: white;
      padding-left: 5px;
      padding-right: 5px;
      color: #919eab;
      outline: none;
      border-color: #e40055;
      box-shadow: none;
    }
    &.has-value:focus ~ label {
      color: #e40055;
    }
  }
}

.input {
  height: 40px;
  border: 1px solid #919eab;
  outline: none;
  box-sizing: border-box;
  border-radius: 4px;
  margin: 8px 0;
  padding: 0 10px;
  font-size: 14px;
  line-height: 16px;
  color: #919eab;
}

