          .text {
            padding-bottom: 18px;
            color: #667c8a !important;
            font-weight: 500;
            font-family: "Metropolis";
            font-size: 18px;
            line-height: 21px;
          }
          .item {
            padding: 0 16px;
            a {
              font-family: "Metropolis";
              font-style: normal;
              font-weight: 500;
              font-size: 16px;
              line-height: 19px;
              color: #667c8a;
            }
            button {
              border: none;
              outline: none;
              font-family: "Metropolis";
              font-style: normal;
              font-weight: 500;
              font-size: 16px;
              line-height: 19px;
              color: #667c8a;
              background-color: transparent;
            }
          }
        }
        .opt {
          position: absolute;
          top: 0;
          width: 100%;
          color: #667c8a;
          padding: 10px;
          border: 0.5px solid #637381;
          box-sizing: border-box;
          border-radius: 3px;
          display: flex;
          flex-flow: column;
          align-items: center;
          background-color: white;
          z-index: 80;
          overflow-y: auto;
          height: 200px;
          // justify-content: center;
          input {
            font-family: "Metropolis";
            font-style: normal;
            font-weight: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 21px;
            width: 100%;
            padding: 10px;
            color: #667c8a;
            border: none;
            background-color: transparent;
            margin-bottom: 10px;
            box-sizing: border-box;
            border-radius: 3px;
            outline: none;
            cursor: pointer;
            &:hover {
              background-color: #667c8a;
              color: white;
            }
            &:nth-child(4) {
              margin-bottom: 0;
            }
          }
        }
        h1 {
          width: 100%;
          font-weight: normal;
          font-size: 15px;
          line-height: 17px;
          margin: 0;
          color: #667c8a;
          cursor: pointer;
          padding: 10px;
          border: 1px solid transparent;
          box-sizing: border-box;
          border-radius: 3px;
          display: flex;
          align-items: center;
          background-color: white;
        }
        h5 {
          position: absolute;
          width: fit-content;
          background-color: white;
          margin: 0;
          padding: 2px;
          font-weight: normal;
          font-size: 12px;
          line-height: 14px;
          z-index: 60;
          color: #667c8a;
          top: -7.5px;
          left: 9px;
        }
        p {
          width: 100%;
          font-weight: normal;
          font-size: 18px;
          line-height: 21px;
          margin: 0;
          color: #667c8a;
          cursor: pointer;
          padding: 11px 14px;
          border: 0.5px solid #637381;
          box-sizing: border-box;
          border-radius: 3px;
          display: flex;
          align-items: center;
        }
        .circle {
          width: 15px;
          height: 15px;
          margin-right: 5px;
          margin-bottom: 1.5px;
        }
        .spanRight {
          position: absolute;
          right: 7%;
        }
      }
      .textInside {
        width: 100%;
        display: flex;
        // align-items: center;
        justify-content: space-between;
        .textInput {
          width: 48%;
        }
      }
      .textInput {
        width: 100%;
      }
      .error-text {
        color: red;
        font-size: 16px;
        // text-align: center;
        &:nth-child(4) {
          // margin: 0 auto;
          margin-top: 10px;
        }
      }
      .textInput {
        .error-text {
          color: red;
          font-size: 10px;
        }
      }
      .text-input {
        width: 100%;
        input:-webkit-autofill {
          box-shadow: 0 0 0px 1000px #fff inset;
          -webkit-text-fill-color: #667c8a;
          transition: background-color 5000s ease-in-out 0s;
        }
        label {
          position: absolute;
          top: 20px;
          left: 15px;
          padding: 0;
          pointer-events: none;
          transition: 0.5s;
          z-index: 10;
          margin-bottom: 0;
          font-family: "Metropolis";
          font-style: normal;
          span {
            color: #e40055;
          }
        }
        label {
          font-weight: normal;
          font-size: 14px;
          line-height: 16px;
          color: #667c8a;
        }
        .input {
          border-radius: 3px;
          min-height: 40px;
          height: max-content;
          padding: 11px 15px;
          font-weight: 500;
          font-family: "Metropolis";
          font-size: 14px;
          line-height: 16px;
          border: 1px solid #667c8a;
          color: #667c8a !important;

          &:focus ~ label,
          &:not([value=""]) ~ label {
            top: 0px;
            left: 8px;
            font-size: 12px;
            line-height: 14px;
            background-color: white;
            padding-left: 5px;
            padding-right: 5px;
            height: none;
            color: #667c8a;
          }

          // &:focus ~ label {
          //   color: #ba7f45;
          // }

          // &:focus,
          // &:valid ~ label,
          // &:not([value=""]) ~ label {
          //   outline: none;
          //   border-color: #ba7f45;
          //   box-shadow: none;
          // }
        }
      }
      .redBtn {
        margin-top: 17px;
        &:disabled {
          background-color: #d9d9d9;
          cursor: not-allowed;
        }
      }
    }
  }
}
